function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, title }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                {/* <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col> */}
                <Col span={3}>
                    <p style={{ fontSize: "1.3rem", width: "150px" }}>{title} details</p>
                </Col>
                <Col span={5}>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected product attributes ?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, userId } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [ln, setLn] = useState('en');
    const [changeForm, setChangeForm] = useState(false);

    const handleChange = (value) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { userId }
            );
            if (!dt?._id && addAccess) {
                setChangeForm(true);
            } else if (dt._id && editAccess) {
                setChangeForm(true);
            } else {
                setChangeForm(false);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.orderDetailDetails(data._id).then(res => {
            setData(res.data || {});
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        if (changeForm) {
            setAjxRequesting(true);
            service.orderDetailSave(data, { ln }).then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                if (ln === "en") {
                    // list();
                }
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        }

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [ln, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setLn('en');
        }
    }, [open]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Order'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={900}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    changeForm ? <Button key="save" type="primary" onClick={save}>Save</Button> : null,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical" disabled={!changeForm}>
                        <Row gutter={[12, 2]}>
                            <Col span={12}>
                                <Form.Item label="Product Id" required>
                                    <Input placeholder="Value" value={data?._id} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Product Name" required>
                                    <Input placeholder="Value" value={data?.productId?.name} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Price" required>
                                    <Input type="number" placeholder="Price" value={data.price} onChange={e => { handleChange({ price: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Actual Price" required>
                                    <Input type="number" placeholder="Actual price" value={data.actualPrice} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Quantity" required>
                                    <Input type="number" placeholder="Quantity" value={data.quantity} onChange={e => { handleChange({ quantity: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Total " required>
                                    <Input type="number" placeholder="Total" value={data.total} onChange={e => { handleChange({ total: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>

            </Modal>
        </>
    );
});

